<template>
  <div>
    <!--<input type="text" class="rex-test" v-model="str" @input = rexTest()>
    <span class="tip" v-show="!isTrue">*</span>-->
    <div class="el-test">
      <el-form ref="form" :model="form" label-width="20%">
        <el-form-item label="活动名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="活动区域">
          <el-select v-model="form.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动时间">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="即时配送">
          <el-switch v-model="form.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="活动性质">
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
            <el-checkbox label="地推活动" name="type"></el-checkbox>
            <el-checkbox label="线下主题活动" name="type" class="no-margin"></el-checkbox>
            <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="特殊资源">
          <el-radio-group v-model="form.resource">
            <el-radio label="线上品牌商赞助"></el-radio>
            <el-radio label="线下场地免费"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="活动形式">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: "test",
    components:{
    },
    data(){
      return{
        isTrue: true,
        str: '',
        form:{
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    mounted() {
      let a = "ghfasfghjkjksjs";
      let obj = {};
      let max = {n:0, char: ''};
      a = a.split('');
      for(let i in a){
        obj[a[i]] = ++obj[a[i]] || 1;
      }

      for(let i in obj){
        if(obj[i] > max.n){
          max.n = obj[i];
          max.char = i;
        }
      }
      console.log(max);
    },
    watch: {
      str: ()=>{
        console.log("kk");
      },
      form:{
        handler(val, oldVal){
          console.log("111");
        },
        deep:true
      }
  },
    methods: {
      rexTest(){
        let rex = /^1[3568][0-9]{9}$/g;
        if(rex.test(this.str))
        {
          this.isTrue = true;
          console.log("1");
        }
        else{
          this.isTrue = false;
          console.log(this.str);
        }
      },
      onSubmit(){
        console.log(this.form);
      }
    }
  }
</script>

<style lang="scss">
.rex-test{
  border: 1px solid #888888;
  width: 260px;
  height: 40px;
  text-indent: 15px;
  margin: 40px auto;
}
  .tip{
    color: red;
    font-size: 18px;
  }
  .el-test{
    padding: 20px 4%;
    .el-form-item__label{
      padding: 0;
      text-align: left;
    }
    .line{
      text-align: center;
    }

    @media screen and (max-width: 767px) and (min-width: 321px){
      .no-margin{
        margin-left: 0;
      }
    }
    @media screen and (max-width: 320px){
      .el-checkbox+.el-checkbox{
        margin-left: 0;
        width: 60%;
      }
    }
  }
</style>



